export const StarRating = ({ starLimit, starSelected, onRate }) => {
  const stars = [...new Array(starLimit)].map((star, i) => {
    const starClass = i < starSelected - 1 ?
      "star selected":
      "star"
    return (
      <div className={starClass} />
    )
  })

  return (
    <div className="star-rating-container">
      { stars }
    </div>
  )
}



